.menu-btn {
  width: 80px;
  height: 80px;
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  transition: all .2s ease-in-out;
  border:3px solid #fff;
  display:none;
}

.menu-btn__burger {
  width: 50px;
  height: 10px;
  background: rgb(228, 63, 57);
  border-radius: 5px;
  transition: all .2s ease-in-out;
}

.menu-btn__burger::before,
.menu-btn__burger::after {
  content: '';
  position: absolute;
  left:12px;
  width: 50px;
  height: 10px;
  background: rgb(155, 41, 41);
  border-radius: 5px;
  transition: all .2s ease-in-out;
}

.menu-btn__burger::before {
  
  transform: translateY(-16px);
}

.menu-btn__burger::after {
  transform: translateY(16px);
}


.menu-btnOpen .menu-btn__burger {
  transform: translateX(-50px);
  background: transparent;
}

.menu-btnOpen .menu-btn__burger::before {
  transform: rotate(45deg) translate(35px, -35px);
  background: #ffffff;
}

.menu-btnOpen .menu-btn__burger::after {
  transform: rotate(-45deg) translate(35px, 35px);
  background: #ffffff;
}

.menu-btnOpen{
  border: 3px solid #ffffff;
}

